<template>
  <div class="menu-box">
    <div class="menu-logo">
      <img src="https://pic1.zhimg.com/80/v2-787f1b120421ce587c40c3ad2f713a94_720w.jpg">
    </div>
    <div class="menu-nav">
      <ul>
        <li v-for="(menu, index) in menuList"
            @click="clickMenuItem(index)"
            :key="menu.id">
          <router-link
            :to="menu.path"
            :class="index === menuActivation ? 'menu-nav-activation': ''">
            {{ menu.name }}
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MenuBar',
  data () {
    return {
      menuActivation: 0,
      menuList: [
        {'id': 1, 'name': '首页', path: '/'},
        {'id': 2, 'name': '分类', path: '/category'},
        {'id': 3, 'name': '标签', path: '/tag'},
        {'id': 4, 'name': '关于', path: ''},
        {'id': 5, 'name': '搜索', path: ''}
      ]
    }
  },
  methods: {
    clickMenuItem (index) {
      this.menuActivation = index
    }
  }
}
</script>

<style scoped>
.menu-logo{
  width: 60px;
  height: 60px;
  border-radius: 5px;
  float: left;
  margin-top: 20px;
  margin-left: 20%;
}
.menu-logo>img{
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.menu-nav{
  width: auto;
  height: 30px;
  float: left;
  margin-top: 35px;
  margin-left: 30%;
}
.menu-nav>ul>li{
  float: left;
  transition: .7s;
}
.menu-nav>ul>li>a{
  padding: 4px 30px;
  margin-left: 10px;
  border-radius: 8px;
  line-height: 30px;
  font-size: 16px;
  border: #fff solid 2px;
}
.menu-nav-activation{
  border-top-color: #a5673f !important;
  border-bottom-color: #6739b6 !important;
  border-left-color: #9c26b0 !important;
  border-right-color: #e03997 !important;
}
.menu-nav>ul>li>a:hover{
  border-top-color: #1890ff;
  border-bottom-color: #ff4d4f;
  border-left-color: #f37b1d;
  border-right-color: #fbbd08;
}
</style>



