        {'id': 3, 'src': 'https://5b0988e595225.cdn.sohucs.com/images/20171124/d8cf4326203b41f592df25d9d326e9fa.jpeg', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'},
        {'id': 4, 'src': 'http://5b0988e595225.cdn.sohucs.com/images/20180115/79e7fa5cec7240c7a60d749c5f69ad16.jpeg', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'}
      ]
    }
  },
  components: {
    LeftContentBar,
    RightContentBar
  }
}
