<style scoped>
.content-big-img>img{
  width: 100%;
  height: 500px;
}
.content-sub-title-img{
  height: 210px;
}
.content-sub-title-img>ul>li{
  float: left;
  width: 25%;
  padding: 17px 11px 10px;
  cursor: pointer;
}
.content-sub-title-img>ul>li .sub-title{
  position: relative;
}
.sub-title>img{
  position: absolute;
  width: 100%;
  height: 180px;
  border-radius: 5px;
}
.sub-title>div{
  position: absolute;
  width: 100%;
  height: 180px;
  background: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  text-align: center;
  transition: .5s;
}
.sub-title>div>div{
  width: 70%;
  margin: 20% auto;
}
.sub-title>div>div>span{
  padding: 1px 6px;
  font-size: 18px;
}
.sub-title>div>div>p{
  font-size: 14px;
}
.sub-title>div:hover{
  background: rgba(0, 0, 0, .5);
  color: #fff;
  border-color: #fff;
}
.content-text-box>div{
  float: left;
}
.content-text-box>div:first-child{
  width: 70%;
}
.content-text-box>div:last-child{
  width: 29%;
  margin-left: 1%;
}
</style>



