<template>
  <div id="components-layout-demo-basic">
    <div id="return">
      <a-popover>
        <template slot="content">
          <p>50%</p>
        </template>
        <a href="#"><a-icon type="vertical-align-top" /></a>
      </a-popover>
    </div>
    <a-layout>
      <a-layout-header>
        <MenuBar/>
      </a-layout-header>
      <a-layout-content>
        <div id="content-box">
          <router-view/>
        </div>
      </a-layout-content>
      <a-layout-footer><FooterBar/></a-layout-footer>
    </a-layout>
  </div>
</template>

<script>
import MenuBar from '../components/menu/MenuBar'
import Main from './Main'
import LeftContentBar from '../components/content/left/LeftContentBar'
import RightContentBar from '../components/content/right/RightContentBar'
import FooterBar from '../components/footer/FooterBar'
export default {
  name: 'Index',
  data () {
    return {
    }
  },
  components: {
    MenuBar,
    Main,
    LeftContentBar,
    RightContentBar,
    FooterBar
  }
}
</script>

<style scoped>
#return{
  position: fixed;
  width: 30px;
  height: 30px;
  border: #1890ff solid 1px;
  right: 90px;
  bottom: 100px;
  text-align: center;
}
#return>a{
  font-size: 20px;
}
#components-layout-demo-basic .ant-layout-header,
#components-layout-demo-basic .ant-layout-footer {
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
  height: 100px;
  line-height: normal;
}
#components-layout-demo-basic .ant-layout-content {
  background: #fff;
}
#content-box{
  width: 76%;
  height: auto;
  margin: 10px auto;
}
.ant-layout{
  background: #fff;
}
#components-layout-demo-basic .ant-layout-footer {
  background: #000;
  margin-top: 200px;
  height: 500px;
}
</style>



